.primary-button {

  font-family: "oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  font-size: 14px;
  line-height: 14px;
  color: rgba(255, 255, 255, 1);
  border: 0;
  padding-top: 7px;
  padding-bottom: 7px;
  text-align: center;
  text-transform: uppercase;
  background-color: rgba(104, 183, 56, 1);
  text-decoration: none;
}
