.main-header {
  display: flex;
  width: 320px;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.main-header__logo-picture {
  display: block;
  margin-left: 20px;
  margin-top: 10px;
  margin-bottom: 18px;

  &:hover {
    opacity: 80%;
  }

  &:active {
    opacity: 60%;
  }
}

.main-header__logo-text {
  display: block;
  margin-left: 57px;
  margin-right: 65px;

  &:hover {
    opacity: 80%;
  }

  &:active {
    opacity: 60%;
  }
}

.main-header__logo-tablet {
  display: none;
}

.main-header__logo-desktop {
  display: none;
}

@media (min-width: $tablet-width) {
  .main-header__logo-tablet {
    display: block;
    width: 174px;
  }

  .main-header__logo-mobile {
    display: none;
  }

  .main-header__logo-text {
    display: none;
    margin-left: 0;
    margin-right: 0;
  }

  .main-header {
    display: flex;
    width: 768px;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }

  .main-header__logo-picture {
    display: block;
    margin-left: 0;
    margin-top: 0;
    margin-bottom: 0;
}
}

@media (min-width: $desktop-width) {
  .main-header__logo-tablet {
    display: none;
    width: 174px;
  }

  .main-header__logo-desktop {
    display: block;
    width: 174px;
  }

  .main-header__logo-mobile {
    display: none;
  }

  .main-header__logo-text {
    display: none;
    margin-left: 0;
    margin-right: 0;
  }

  .main-header {
    display: flex;
    width: 1440px;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }

  .main-header__logo-picture {
    display: block;
    margin-left: 0;
    margin-top: 0;
    margin-bottom: 0;
}
}
