.comments__title {
  font-family: "oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  font-size: 24px;
  line-height: 26px;
  color: rgba(0, 0, 0, 1);
  text-transform: none;
}

.comments__input {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: $tablet-width) {
  .comments__title {
    font-family: "oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    font-size: 36px;
    line-height: 36px;
    color: rgba(0, 0, 0, 1);
    text-transform: none;
  }

  .comments__input {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
